<template>
  <div>
    <!-- 顶部 -->
    <van-nav-bar title="购物车" />

    <!-- 购物车空空 -->
    <div class="item-empty" v-show="bool">
      <img src="https://jp.juancdn.com/jpwebapp_v1/images_v1/shopping/empty-cart.png" alt />
      <div class="b-title">看到喜欢的就带回家吧</div>
      <div class="btnBc" @click="$router.push('/home')">今日推荐</div>
    </div>

    <!-- 购物车列表 -->
    <div class="shoppbox" v-for="(item,index) in $store.state.shoppingcart.list" :key="index">
      <van-checkbox class="circular" v-model="item.bool"></van-checkbox>
      <van-card
        :price="item.price"
        desc="描述信息"
        :title="item.name"
        :thumb="item.img"
        @click-thumb="details"
      >
        <div slot="tags">
          <van-tag plain @click="item.num <=1 ? 1: item.num--">-</van-tag>
          <input type="text" v-model="item.num" class="num" />
          <van-tag plain @click="item.num++">+</van-tag>
        </div>
        <div slot="footer">
          <!-- <van-checkbox class="checkboxbutton" v-model="item.isSelect"></van-checkbox> -->
          <van-button size="mini" @click="del(index)" class="delbutton">删除</van-button>
        </div>
      </van-card>
    </div>

    <van-submit-bar
      v-show="!bool"
      :price="$store.getters['shoppingcart/total']"
      button-text="结算"
      @submit="toOrderSubm"
    >
      <van-checkbox v-model="isbool">全选</van-checkbox>
      <!-- <span slot="tip">
        你的收货地址不支持同城送,
        <span>修改地址</span>
      </span>-->
    </van-submit-bar>

    <div class="poputer" v-show="show">{{text}}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      text: null,
      show: false,
      setTiem: null
    };
  },
  methods: {
    details() {
      this.$store.state.bool = false;
      this.$router.push("/details");
    },
    del(index) {
      this.$store.state.shoppingcart.list.splice(index, 1);
    },
    toOrderSubm() {
      if (this.setTiem == null) {
        if (this.$store.state.shoppingcart.list.length == 0) {
          this.text = "购物车空空";
          this.show = true;
          this.setTiem = setTimeout(() => {
            this.show = false;
            this.setTiem = null;
          }, 2000);
          return;
        }

        let boolEit = this.$store.state.shoppingcart.list.some(item => {
          return item.bool;
        });
        if (boolEit) {
          this.$router.push("/orderSubm");
        } else {
          this.text = "您还没有选择宝贝哦";
          this.show = true;
          this.setTiem = setTimeout(() => {
            this.show = false;
            this.setTiem = null;
          }, 2000);
          return;
        }
      }
    }
  },
  computed: {
    isbool: {
      get() {
        return this.$store.state.shoppingcart.list.every(item => {
          return item.bool;
        });
      },
      set(value) {
        return this.$store.state.shoppingcart.list.forEach(item => {
          return (item.bool = value);
        });
      }
    },
    bool() {
      return this.$store.state.shoppingcart.list.length < 1;
    }
  },
  created(){
    this.$store.state.bool = true;
  }
};
</script>

<style lang="scss" scoped>
.van-nav-bar__title {
  font-size: 18px;
}
.van-nav-bar__text {
  font-size: 15px;
  color: #4a4a4a;
}
.van-card__desc {
  color: #bbb;
}
.van-card {
  font-size: 14px;
}
.van-card__price {
  color: #ff464e;
}
.van-submit-bar {
  bottom: 50px;
}
.van-card {
  padding-left: 45px;
}
.shoppbox {
  position: relative;
}
.circular {
  position: absolute;
  top: 50%;
  left: 12px;
  z-index: 11;
  margin-top: -10px;
}
.goods-card {
  margin: 0;
  background-color: #fff;
}

.delete-button {
  height: 100%;
}
.head_radio {
  display: inline-block;
}
.item_head_box {
  height: 44px;
  padding-left: 12px;
  color: #666;
  .imgiconBox {
    margin-left: 10px;
    margin-right: 8px;
    display: inline-block;
    width: 25px;
    height: 25px;
    img {
      width: 100%;
    }
  }
  .text {
    font-size: 14px;
  }
}
.num {
  width: 50px;
  text-align: center;
}
.poputer {
  padding: 10px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.item-empty {
  width: 100%;
  text-align: center;
  margin: 30px 0;
  img {
    display: block;
    margin: 0 auto;
    margin-bottom: 12px;
    width: 140px;
    height: auto;
  }
  .b-title {
    font-size: 16px;
    color: #333;
    margin-bottom: 18px;
  }
  .btnBc {
    width: 138px;
    height: 40px;
    margin: 0 auto;
    line-height: 40px;
    font-size: 15px;
    color: #ff464e;
    border: 1px solid currentColor;
    text-align: center;
  }
}
</style>